.auto-scroll-container {
  overflow: hidden;
  width: 100%;
  position: relative;
}

.auto-scroll-text {
  white-space: nowrap;
  display: inline-block;
  min-width: 100%;
}

.auto-scroll-text.scrolling {
  animation: auto-scroll var(--scroll-duration, 8s) linear infinite;
  animation-delay: 2s;
}

.auto-scroll-text.scrolling.paused {
  animation-play-state: paused;
}

@keyframes auto-scroll {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(0);
  }
  75% {
    transform: translateX(var(--scroll-distance, -50%));
  }
  100% {
    transform: translateX(var(--scroll-distance, -50%));
  }
}